<template>
    <header>
        <div class="menu-box">
            <RouterLink to="/" class="top-name" style="margin: 0px 10px">„Éõ„Éº„É†</RouterLink>
            <!-- Ê§úÁ¥¢Ê¨Ñ -->
            <div class="search-box">
                <input type="text" class="search-input" v-model="itemName">
                <span class="search-btn" @click="itemSeach">üîé</span>
            </div>
            <!-- „Ç¢„Ç§„ÉÜ„É†Áµû„ÇäËæº„Åø -->
            <select class="top-genre" @change="itemGenre">
                <option value="">---</option>
                <option value="all">„Åô„Åπ„Å¶</option>
                <option :value="genre" v-for="genre in genreList.keys()" :key="genre">{{ genreList.get(genre) }}</option>
            </select>
            <!-- „Ç¢„Ç§„ÉÜ„É†ÊÉÖÂ†± -->
            <div class="select-box">
                <RouterLink :to="!isShow ? '/cartConfirm' : '/login'" class="select-item">üõí</RouterLink>
                „Ç´„Éº„Éà
            </div>
            <div class="select-box">
                <RouterLink :to="!isShow ? '/history' : '/login'" class="select-item">üïê</RouterLink>
                <div>Â±•Ê≠¥</div>
            </div>
            <div class="select-box">
                <RouterLink :to="!isShow ? '/favorite' : '/login'" class="select-item">‚òÜ</RouterLink>
                <div>„ÅäÊ∞ó„Å´ÂÖ•„Çä</div>
            </div>
        </div>
        <!-- „É¶„Éº„Ç∂„ÉºÊÉÖÂ†± -->
        <div v-if="isShow" class="login-box">
            <RouterLink to="/insert" class="top-name">Êñ∞Ë¶èÁôªÈå≤</RouterLink>
            <RouterLink to="/login" class="top-name">„É≠„Ç∞„Ç§„É≥</RouterLink>
        </div>
        <div v-else class="login-box" @mouseleave="userInfoShow = false">
            <div class="top-name" @mouseover="userInfoShow = true">{{ user.name }}„Åï„Çì„Åì„Çì„Å´„Å°„ÅØÔºÅ</div>
            <div class="login-info-area" v-show="userInfoShow">
                <div class="login-info-box">
                    <div class="top-name" @click="router.push('/userEdit')">„É¶„Éº„Ç∂„ÉºÊÉÖÂ†±Á∑®ÈõÜ</div>
                    <RouterLink to="/pointList">{{ point }}„Éù„Ç§„É≥„Éà</RouterLink>
                </div>
            </div>
            <div @click="logOut()" class="top-name">„É≠„Ç∞„Ç¢„Ç¶„Éà</div>
        </div>

    </header>
    
</template>

<script setup>
import axios from 'axios';
import { computed, ref, watch } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

const itemName = ref('');
const genreList = ref(new Map());
const user = computed(() => store.getters.getUserData);
const isShow = computed(() => user.value === null);
const userInfoShow = ref(false);
const point = ref('');

const store = useStore();
const router = useRouter()
// „Ç∏„É£„É≥„É´‰∏ÄË¶ß„ÇíÂèñÂæó
axios.get('/item')
.then(response => {
    response.data.forEach(item => {
        genreList.value.set(item.genre,item.genre)
    })
})
// „É¶„Éº„Ç∂„Éº„ÅÆ„Éù„Ç§„É≥„ÉàÂêàË®à„ÅÆÁ¢∫‰øù
function getPoint(){
    if(user.value != null){
        axios.post('/point',
            {
                userId: user.value.id
            }
        )
        .then(response=>{
            point.value = response.data
        })
    }
}
getPoint()
watch(user, ()=>{
    getPoint()
})
// „Ç¢„Ç§„ÉÜ„É†„ÇíÊ§úÁ¥¢
function itemSeach(){
    axios.post('/item/search',
        {
            name: itemName.value
        }
    )
    .then(response=>{
        itemName.value = ''
        router.push('/item')
        store.dispatch('updateItemListData',response.data);
    })
}
// „Ç¢„Ç§„ÉÜ„É†„ÇíÁµû„ÇäËæº„ÇÄ
function itemGenre(event){
    if(event.target.value === 'all'){
        axios.get('/item')
        .then(response => {
            router.push('/item')
            store.dispatch('updateItemListData',response.data);
        })
    }else{
        axios.post('/item/genre',
            {
                genre: event.target.value
            }
        )
        .then(response => {
            router.push('/item')
            store.dispatch('updateItemListData',response.data);
        })        
    }
}
// „É≠„Ç∞„Ç¢„Ç¶„Éà
function logOut() {
  store.dispatch('updateUserData', null);
  router.push('/login')
}
</script>
<style src="../../css/main/top.css" scoped></style>