<template>
    <div class="cart-main">
        <div class="cart-area">
            <!-- „Ç´„Éº„Éà‰∏ÄË¶ß -->
            <div class="cart-list">
                <h1>„Ç∑„Éß„ÉÉ„Éî„É≥„Ç∞„Ç´„Éº„Éà</h1>
                <div v-if="cartList.length === 0">„Ç´„Éº„Éà„Å´ÂïÜÂìÅ„Åå„ÅÇ„Çä„Åæ„Åõ„Çì</div>
                <div v-for="cart in cartList" :key="cart.id" class="cart-box">
                    <img :src="'/image/' + cart.image" class="cart-image" @click = "itemDetail(cart.itemId)">
                    <div class="cart-info">
                        <div class="cart-info-top">
                            <div class="cart-name">{{ cart.name }}</div>
                            <div class="cart-price">Ôø•{{ cart.price }}</div>
                        </div>
                        <div class="cart-info-bottom">
                            <select @change="countSelect($event,cart)" class="cart-count">
                                <option v-for="num in 50" :key="num" :value="num" :selected="num===cart.count">Êï∞ÈáèÔºö{{ num }}</option>
                            </select>
                            <div class="cart-delete-btn" @click="deleteCart(cart)">ÂâäÈô§</div>
                            <div style="margin: 0px 10px">|</div>
                            <div class="cart-after-btn" @click="afterClick(cart,false)">„ÅÇ„Å®„ÅßË≤∑„ÅÜ</div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- „ÅÇ„Å®„ÅßË≤∑„ÅÜ‰∏ÄË¶ß -->
            <h1>„ÅÇ„Å®„ÅßË≤∑„ÅÜ‰∏ÄË¶ß</h1>
            <div v-if="cartAfterList.length === 0">„ÅÇ„Å®„ÅßË≤∑„ÅÜ„Å´ÈÅ∏Êäû„Åó„ÅüÂïÜÂìÅ„Åå„ÅÇ„Çä„Åæ„Åõ„Çì</div>
            <div class="cart-after-list">
                <div v-for="cart in cartAfterList" :key="cart.id" class="cart-after-box">
                    <img :src="'/image/' + cart.image" class="cart-image" @click = "itemDetail(cart.itemId)">
                    <RouterLink :to="'/detail/'+cart.itemId">{{ cart.name }}</RouterLink>
                    <div class="cart-after-price">Ôø•{{ cart.price }}</div>
                    <div class="cart-return-btn-box">
                        <div class="cart-return-btn" @click="afterClick(cart,true)">„ÇÇ„Å®„Å´Êàª„Åô</div>
                    </div>
                    <div class="cart-after-delete-btn" @click="deleteCart(cart)">ÂâäÈô§</div>
                </div>
            </div>
        </div>
        <!-- ‰æ°Ê†ºÊÉÖÂ†± -->
        <div class="cart-price-box">
            <div class="cart-total-price">Â∞èË®à({{ totalCount }}ÂÄã„ÅÆÂïÜÂìÅ)</div>
            <div class="cart-total-price">ÔºöÔø•{{ totalPrice }}</div>
            <div>{{ totalPrice/100 }}„Éù„Ç§„É≥„ÉàÁç≤Âæó‰∫àÂÆö</div>
            <div>‰ΩøÁî®ÂèØËÉΩ„Éù„Ç§„É≥„ÉàÔºö{{ point }}</div>
            <div class="cart-price-btn-box">
                 <button @click="orderInput" class="cart-price-btn">Ê≥®ÊñáË©≥Á¥∞Á¢∫Ë™ç</button>
            </div>
        </div>
    </div>
</template>
<script setup>
import { computed, onMounted, ref } from 'vue';
import axios from 'axios';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';

const router = useRouter()
const store = useStore()
const user = computed(() => store.getters.getUserData)
const cartList = ref('')
const cartAfterList = ref('')
const totalPrice = ref(0)
const totalCount = ref(0)
const point = ref(0)

// „É¶„Éº„Ç∂„Éº„ÅÆ„Éù„Ç§„É≥„ÉàÂêàË®à„ÅÆÁ¢∫‰øù
axios.post('/point',
    {
        userId: user.value.id
    }
)
.then(response=>{
    point.value = response.data
})

onMounted(() => {
    countData()
})
// „Ç´„Éº„Éà‰∏ÄË¶ß„ÇíÁ¢∫‰øù
function cartListGet(response){
    cartList.value = response.filter(cart => cart.afterFlg === 0);
    cartAfterList.value = response.filter(cart => cart.afterFlg === 1);
}
// „Ç´„Éº„ÉàÂÜÖ„ÅÆÂïÜÂìÅ„ÇíÂâäÈô§
function deleteCart(cart){
    axios.post('/cart/delete',
        {
            id: cart.id,
            userId: user.value.id
        }
    )
    .then(response => {                
        cartListGet(response.data);
        if(cart.afterFlg === 0){
            totalPrice.value -= cart.price * cart.count
            totalCount.value -= cart.count
        }
    })  
}
// „Ç´„Éº„ÉàÂÜÖÂïÜÂìÅ„ÅÆÊï∞„ÇíÊõ¥Êñ∞
function countData(){
    axios.post('/cart',
        {
            id: user.value.id
        }
    )
    .then(response => {
        cartListGet(response.data)
        totalUpdate(response.data)
    })
}
//ÂêàË®àÈáëÈ°ç„Å®ÂêàË®àË≥ºÂÖ•Êï∞„ÇíÊõ¥Êñ∞
function totalUpdate(cartList){
    totalPrice.value = 0
    totalCount.value = 0
    cartList.forEach(cart => {
        if(cart.afterFlg === 0){
            totalPrice.value = totalPrice.value + cart.price * cart.count
            totalCount.value = totalCount.value + cart.count
        }
    });
}
//ÂïÜÂìÅË©≥Á¥∞„ÇíË°®Á§∫
function itemDetail(id){
    router.push('/detail/' + id)
}
//„Ç´„Éº„ÉàÂÜÖ„ÅÆÂïÜÂìÅÊï∞„ÇíÊõ¥Êñ∞
function countSelect(event,cart){
    axios.post('/cart/countUpdate',
        {
            id: cart.id,
            count: event.target.value,
            userId: user.value.id
        }
    )
    .then(response => {
        totalUpdate(response.data)
    })
}
// Ê≥®ÊñáË©≥Á¥∞„Å∏ÈÅ∑Áßª
function orderInput(){
    router.push('/orderInput');
}
// „ÅÇ„Å®„ÅßË≤∑„ÅÜ„Å´ËøΩÂä†„ÄÅ„Ç´„Éº„Éà„Å´Êàª„Åô
function afterClick(cart,exists){
    axios.post('/cart/after',{
        id: cart.id,
        userId: user.value.id,
        afterExists: exists
    })
    .then(response=>{
        cartListGet(response.data)
        totalUpdate(response.data)
    })
}
</script>
<style src="../../../css/cart/confirm.css" scoped></style>